html, body, ul, li, p, img, h1, h2, h3, h4 {
  margin: 0;
  padding: 0; }

ul {
  list-style: none; }

a {
  text-decoration: none;
  transition: 0.5s ease; }

img {
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
  border: 0;
  vertical-align: middle; }

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/opensans-400-normal.woff2") format("woff2"), url("../fonts/opensans-400-normal.woff") format("woff"), url("../fonts/opensans-400-normal.ttf") format("truetype");
  font-weight: 400;
  font-style: normal; }

html {
  font: 20px 'Open Sans', sans-serif;
  color: #252525; }

.justify {
  text-align: justify;
  font-size: 1px;
  line-height: 0;
  text-justify: newspaper;
  zoom: 1; }
  .justify:after {
    width: 100%;
    height: 0;
    visibility: hidden;
    overflow: hidden;
    content: '';
    display: inline-block; }

.promo__slogan {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%); }

.wraper {
  position: relative;
  width: 1200px;
  margin: 0 auto; }

.button {
  display: inline-block;
  cursor: pointer;
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  text-align: center;
  white-space: nowrap;
  color: #323232;
  font-size: 24px;
  line-height: 48px;
  height: 50px;
  padding: 0 11px;
  border-radius: 4px; }
  .button_promo {
    background-color: #e23101;
    background-color: #357ebd;
    color: #fff;
    margin: 30px 0 0 0; }

.header {
  font-size: 40px;
  line-height: 1.3;
  margin: 0 0 50px 0; }

.text {
  line-height: 26px; }

/*head*/
.head {
  overflow: hidden;
  box-shadow: 0px 6px 6px -5px rgba(0, 0, 0, 0.36);
  height: 70px;
  color: #fff;
  position: fixed;
  width: 100%;
  z-index: 2;
  top: 0;
  left: 0;
  background: #fff; }
  .head:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1; }
  .head.white-block {
    color: #252525; }
    .head.white-block:after {
      background-color: transparent; }
    .head.white-block .menu__link {
      color: #252525; }
    .head.white-block .phone {
      color: #252525; }

.logo {
  float: left;
  width: 200px;
  z-index: 3; }

.menu {
  position: relative;
  z-index: 2;
  height: 100%;
  margin: 0 280px 0 200px;
  text-align: center; }
  .menu__list {
    display: inline-block;
    vertical-align: middle;
    line-height: 70px; }
  .menu__item {
    display: inline-block;
    margin-left: 20px; }
  .menu__link {
    color: #fff; }

.phone {
  position: relative;
  z-index: 2;
  display: table;
  height: 60px;
  float: right;
  width: 280px;
  border-left: 1px dotted #555;
  padding: 0 0 0 30px;
  color: #fff;
  box-sizing: border-box;
  margin: 5px 0;
  transition: 0.5s ease; }
  .phone__number {
    display: table-cell;
    vertical-align: middle; }

/*head end*/
/*promo*/
.promo {
  position: relative;
  margin: 70px 0 0 0;
  text-align: left;
  color: #fff;
  overflow: hidden;
  height: 400px;
  background: url(../img/promo.jpg) no-repeat 50% 0px;
  background-size: cover; }
  .promo:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5); }
  .promo .wraper_promo {
    height: 100%; }
  .promo__content {
    position: relative; }
  .promo__slogan {
    right: 100px;
    z-index: 1; }
  .promo__images {
    width: 100%; }

/**/
/*service advantages*/
.advantages {
  font-size: 0px;
  text-align: center;
  padding: 50px 0 0 0; }

.service {
  display: inline-block;
  width: 27%;
  margin: 0 3%;
  box-sizing: border-box;
  vertical-align: top;
  text-align: center; }
  .service__images {
    width: 150px; }
  .service__title {
    margin-top: 20px;
    margin-bottom: 30px;
    font-size: 25px;
    line-height: 1.2;
    color: #363636; }

/*advantages end*/
/*gallery*/
.gallery {
  font-size: 0px;
  text-align: center;
  background-color: #f2f2f2;
  padding: 50px 0 0 0; }

.photo {
  display: inline-block;
  position: relative;
  width: 25%;
  box-sizing: border-box;
  padding: 10px; }
  .photo__title {
    font-size: 18px;
    background: #452e2e;
    letter-spacing: -0.2px;
    padding: 10px 0;
    color: #fff;
    font-style: italic; }
  .photo__link {
    display: block; }
    .photo__link:after {
      content: '';
      position: absolute;
      top: -5px;
      left: -7px;
      width: 0;
      height: 0;
      border: 15px solid transparent;
      border-bottom: 15px solid #F2F2F2;
      transform: rotate(-45deg); }

.row-photo {
  border-bottom: 1px solid #d1d1d1;
  padding: 0 0 25px 0; }
  .row-photo:last-child {
    border: none;
    padding: 25px 0; }

/*calculator*/
.calculator {
  margin: 50px 0 0 0;
  text-align: center; }

.wraper_calculator {
  background-color: #EFE9DD;
  padding: 25px 0; }

.header_calculator {
  margin: 0 0 25px 0; }

.form-calculator {
  width: 850px;
  margin: 0 auto; }

.item-form {
  display: inline-block; }
  .item-form__title {
    display: inline-block; }
  .item-form__field {
    display: inline-block;
    padding: 0px 38px 0 35px;
    height: 36px;
    margin: 0 30px 0 0;
    border: 1px solid #ddd5c3; }
    .item-form__field_fireplace {
      background: url(../img/arrow.png) no-repeat right 0px, url(../img/arrow.png) no-repeat left -124px, #fff; }
    .item-form__field_WC {
      background: url(../img/arrow.png) no-repeat right 0px, url(../img/arrow.png) no-repeat left -43px, #fff; }
    .item-form__field_bedroom {
      background: url(../img/arrow.png) no-repeat right 0px, url(../img/arrow.png) no-repeat left -81px, #fff; }

.checkbox {
  display: inline-block; }

.select-arrow {
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none; }
  .select-arrow::-ms-expand {
    display: none; }

.price {
  text-align: right;
  margin: 0 45px 0 0; }
  .price__item {
    padding: 25px 0 0 0; }
  .price__value {
    font-size: 25px;
    font-weight: 700; }

/*calculator end*/
/*footer*/
.footer {
  padding: 50px 0 25px 0; }

.copyright {
  text-align: center; }
  .copyright__item {
    font-size: 14px;
    line-height: 1.4; }
    .copyright__item__develop {
      font-size: 14px; }
  .copyright a {
    color: #252525; }

/**/
/*media*/
